
.point.active {
  background: #2d8cf0;
}
.m-routerTags {
  height: 64px;
  overflow: hidden;
  width: 100%;
  /*background-color: #fff;*/
}
.m-routerTags-wrapper {
  position: absolute;
  left: 0;
  top: 0;
  overflow-x: auto;
  white-space: nowrap;
  /*background-color: #fff;*/
  /*padding: 10px 15px;*/
  box-sizing: border-box;
  /*.el-tag .el-icon-close{

    }*/
}
.m-routerTags-wrapper .el-tag {
  background-color: #fafafa;
  display: inline-block;
  padding: 0 10px;
  height: 64px;
  line-height: 64px;
  font-size: 1.4rem;
  color: #bebebe;
  border-radius: 0px;
  box-sizing: border-box;
  border: 1px solid #e0e0e0;
  border-top: none;
  border-left: none;
  white-space: nowrap;
  cursor: pointer;
}
.m-routerTags-wrapper .el-tag.active {
  background: #fff;
  color: #1cc87e;
  border-bottom: #fff;
  padding: 0 10px;
}
.m-routerTags-wrapper .tagText {
  display: inline-block;
}
.m-routerTags-wrapper .el-tag + .el-tag {
  margin-left: 0;
}



