
.nav-logo {
  display: flex;
  color: white;
}
.nav-logo .logo-left .icon-logo {
  width: 40px;
  height: 40px;
  background-size: 100% 100%;
}
.nav-logo .logo-right .side-select {
  width: 80%;
}
.nav-logo .logo-right .side-select .el-input--suffix .el-input__inner {
  color: #fff;
  border: 0!important;
  font-size: 12px;
}
.nav-logo .logo-right .side-select .el-input--small .el-input__icon {
  line-height: 22px;
}
.nav-logo .logo-right .side-select .el-input__inner {
  height: 22px;
  line-height: 22px;
}
.nav-logo .logo-right p {
  text-align: center;
  margin: 0;
}
.nav-line {
  background: url("../assets/ico/sidebar-line.png") repeat-x;
  height: 4px;
  width: 100%;
}
.nav-footer {
  position: absolute;
  width: 100%;
  height: 80px;
  left: 0;
  bottom: 0;
  color: #929292;
  font-size: 12px;
  background: #4a4a4a;
}
.nav-footer .flow-icon {
  width: 15px;
  margin: 5px auto;
}
.nav-footer .brand-icon {
  position: absolute;
  bottom: 5px;
}
.footer-icon {
  width: 25px;
  margin: 5px 1px;
  vertical-align: middle;
}
.version-icon {
  margin-left: 5px;
  cursor: pointer;
}
.menu-wrap {
  position: absolute;
  top: 55px;
  bottom: 80px;
  width: 140%;
  overflow-y: auto;
  cursor: pointer;
}
.menu-wrap .menu-title {
  color: #7a7a7a;
  font-size: 12px;
  padding: 6px 0 6px 16px;
}
.menu-wrap .main-menu,
.menu-wrap .collect-menu {
  color: white;
}
.menu-wrap .main-menu .menu-item,
.menu-wrap .collect-menu .menu-item {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 12px 0 12px 11px;
}
.menu-wrap .main-menu .menu-item i,
.menu-wrap .collect-menu .menu-item i {
  margin-right: 8px;
}
.menu-wrap .main-menu .menu-item i:last-child,
.menu-wrap .collect-menu .menu-item i:last-child {
  font-size: 12px;
  position: absolute;
  right: 5px;
  line-height: 20px;
}
.menu-wrap .main-menu .menu-item:hover,
.menu-wrap .collect-menu .menu-item:hover {
  background-color: #46987c;
}
.menu-wrap .main-menu .activeClass,
.menu-wrap .collect-menu .activeClass {
  background-color: #00C26F;
}
.connect-wrap {
  position: absolute;
  bottom: 40px;
  left: 15%;
}
.connect-wrap a {
  color: #929292;
}



