/*折叠按钮*/
.el-sidebar-fold {
  position: absolute;
  height: 64px;
  width: 62px;
  background: #fafafa;
  color: #aeb9c2;
  text-align: center;
  line-height: 70px !important;
  font-size: 12px;
  user-select: none;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  display: inline-block;
  z-index: 1;
  border-bottom: 1px solid #e0e0e0;
  border-right: 1px solid #e0e0e0;
}
/*一级导航*/
.el-menu-leftside {
  background-color: #4a4a4a;
}
.el-menu-leftside .el-menu-item,
.el-menu-leftside .el-submenu__title {
  font-weight: bold;
  color: #fff;
  font-size: 12px;
}
.el-menu-leftside .el-submenu .el-menu {
  background-color: #333743;
}
.el-menu-leftside .el-menu-item:hover,
.el-menu-leftside .el-submenu__title:hover,
.el-menu-leftside .el-submenu .el-menu .el-menu-item:hover,
.el-menu-leftside .el-menu--horizontal .el-submenu {
  color: #fff;
  background-color: #46987c;
}
.el-menu-leftside .secondMenu .el-menu-item.is-active,
.el-menu-leftside .el-submenu.is-active,
.el-menu-leftside .el-submenu.is-opened {
  color: #fff;
  background: #00c26f !important;
}
.el-menu-leftside .el-menu-item .nav-icon,
.el-menu-leftside .el-submenu .nav-icon {
  width: 50px;
  height: 50px;
  margin-top: -25px;
  position: absolute;
  top: 50%;
}
