    transform: rotate(-34deg);
    opacity: 1;
}
10% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    opacity: 1;
}
15% {
    -webkit-transform: rotate(-34deg);
    transform: rotate(-34deg);
    opacity: 0;
}
}
