  position: relative;
}
.common-wrap .content-head-x .head-form-title span:first-child {
  font-size: 22px;
}
.common-wrap .content-head-x .head-form-title span:last-child {
  position: absolute;
  right: 20px;
}
.common-wrap .content-head-x .head-form-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  width: 100%;
  margin-top: 10px;
  padding-left: 20px;
}
.common-wrap .content-head-x .head-form-content div {
  width: 23%;
  margin-right: 5px;
  margin-bottom: 5px;
}
.common-wrap .content-head-x .head-form-content div span:first-child {
  font-weight: bold;
}
.common-wrap .content-detail {
  padding: 0 20px;
}
.common-wrap .remark-wrap {
  display: flex;
  padding-left: 20px;
}
.common-wrap .remark-wrap div:first-child {
  font-weight: bold;
}
.common-wrap .project-wrap {
  display: flex;
  padding-left: 20px;
  padding-bottom: 4px;
}
.common-wrap .project-wrap div:first-child {
  font-weight: bold;
}
.common-wrap .headspan {
  display: inline-block;
  width: 75%;
  line-height: 16px;
  overflow-y: auto;
  max-height: 40px;
  text-overflow: ellipsis;
}
.common-wrap .common-dialog .el-dialog {
  width: 80%;
}
.common-wrap .common-dialog .common-left-offset.leftDiv {
  left: 86% !important;
}
.common-wrap .common-dialog .el-table--small td {
  padding: 0;
}
.common-wrap .common-dialog-btn .el-button {
  background-color: transparent;
}
.common-wrap .common-dialog-btn .el-button:active,
.common-wrap .common-dialog-btn .el-button:hover,
.common-wrap .common-dialog-btn .el-button:focus,
.common-wrap .common-dialog-btn .el-button:visited {
  background-color: transparent;
}
.common-wrap .common-dialog-btn .icon-confirm {
  background-image: url('assets/images/icon_confirm.png');
}
.common-wrap .common-dialog-btn .icon-close {
  background-image: url('assets/ico/icon_close.png');
}
.common-wrap .common-dialog-btn .icon-dialogI {
  width: 65px;
  height: 59px;
  display: inline-block;
