}
.common-wrap .child-table-search {
  display: flex;
}
.common-wrap .child-table-search .child-table-form {
  width: 90%;
}
.common-wrap .child-table-search .child-table-btn {
  width: 10%;
}
.common-wrap .child-table-wrap {
  position: relative;
  display: flex;
  width: 100%;
  flex-direction: column;
}
.common-wrap .child-table-item {
  width: 50%;
  margin-right: 20px;
}
.common-wrap .child-pagination {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}
.common-wrap .child-table-item-count {
  position: absolute;
  top: 5px;
  z-index: 100;
  left: 300px;
  width: 80%;
  display: flex;
  font-size: 18px;
}
.common-wrap .child-table-item-count div {
  width: 40%;
}
.common-wrap .child-table-item-count div span:first-child {
  font-weight: bold;
}
.common-wrap .child-table-item-count div:last-child {
  width: 20%;
  font-size: 14px;
}
.custom-head {
  display: flex;
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  margin-top: 10px;
}
.custom-head .custom-head-item {
  display: flex;
}
.custom-head .custom-head-item > div {
  display: flex;
}
.custom-head .custom-head-item .head-item-label {
  width: 80px;
  font-weight: bold;
}
.custom-head .custom-head-item .value-I,
.custom-head .custom-head-item .value-II,
.custom-head .custom-head-item .value-III,
.custom-head .custom-head-item .value-IV {
  min-width: 100px;
}
.custom-head .custom-head-item .value-I {
  margin-right: 20px;
}
.custom-head .custom-head-item .label-III {
  margin-right: 10px;
}
.custom-head .custom-head-item .value-III {
  margin-right: 20px;
}
.custom-head .custom-head-item .label-IV {
  margin-right: 10px;
}
@media screen and (max-width: 1500px) {
.custom-head .custom-head-item .head-item-label {
    width: 60px;
}
.custom-head .custom-head-item .label-I,
  .custom-head .custom-head-item .label-II {
    width: 50px;
}
.custom-head .custom-head-item .value-I {
    margin-right: 5px;
}
.custom-head .custom-head-item .label-III {
    margin-right: 0;
}
.custom-head .custom-head-item .value-III {
    margin-right: 5px;
}
.custom-head .custom-head-item .label-IV {
    margin-right: 0;
}
