.l-spa {
  height: 100%;
  padding: 1px;
}
.l-spa > .el-row {
  height: 100%;
  margin: 0;
  padding: 10px 0;
}
.l-spa > .el-row > .el-col {
  position: relative;
}
.l-comp-spa {
  height: 100%;
  padding: 1px;
}
.l-comp-spa > .el-container {
  height: 100%;
}
.comp-alu-form-xs .l-header span {
  display: inline-block;
  margin: 6px 45px 0 10px;
}
.comp-alu-tree-xs .el-tree-node:focus > .el-tree-node__content {
  background-color: #00c26f;
  color: #fff;
}
.comp-alu-tree-xs .el-tree-node:focus > .el-tree-node__content .l-tree-icons i {
  color: #E3FFF5;
}
.comp-alu-tree-xs .el-tree-node__content {
  height: 30px;
}
.comp-alu-tree-xs .el-tree-node__content:hover {
  background-color: #BFE5D6;
}
.comp-alu-tree-xs .el-tree-node__content > .el-tree-node__expand-icon {
  color: #666;
}
.comp-alu-tree-xs .el-tree-node__loading-icon {
  color: #fff;
}
.comp-alu-tree-xs .el-icon-caret-right:before {
  font-size: 15px;
}
.comp-alu-tree-xs .el-tree-node__expand-icon.is-leaf {
  color: transparent;
}
#app {
  height: 100%;
}



