
.sidebar-footer {
  position: absolute;
  left: 10px;
  bottom: 20px ;
  color: #929292;
  font-size: 12px;
}
.sidebar-logo {
  height: 63px;
}
.footer-ico {
  width: 25px;
  margin: 0 5px;
  vertical-align: middle;
}
.logo-txt {
  display: inline-block;
  vertical-align: top;
  color: #888;
  width: 65%;
  margin-top: 15px;
  font-size: 12px;
  /*.sideSelect .el-input{
            font-size: 12px;
        }*/
}
.custom-ico {
  width: 12px;
  height: 12px;
  background: url(../assets/ico/right-arrow.png) no-repeat;
  background-size: 100%;
  display: inline-block;
  position: absolute;
  right: 10px;
  top: 15px;
}
.img-logo {
  width: 25px;
  background-size: 100% 100%;
  display: inline-block;
  margin: 15px 8px 10px ;
}
.sidebar-line {
  background: url("../assets/ico/sidebar-line.png") repeat-x;
  height: 4px;
  width: 100%;
}
.el-menu .nav-icon + .nav-title {
  padding-left: 10px;
}
.el-menu .el-icon-caret-bottom:before {
  content: '';
}
.sideSelect {
  padding-right: 5px;
}
.sideSelect input {
  border: 0;
  color: #fff;
  background-color: transparent;
}
.nav-title {
  margin-left: 8px;
}
/*新增*/
.el-menu--horizontal > .el-menu-item:hover,
.el-menu--horizontal > .el-submenu.is-active .el-submenu__title,
.el-menu--horizontal > .el-submenu:hover .el-submenu__title {
  border-bottom: none;
}
.el-menu--horizontal .el-menu-item:hover,
.el-menu--horizontal .el-submenu__title:hover {
  color: #fff;
  background-color: #46987c;
}
.el-menu--horizontal .el-submenu .el-menu-item,
.el-menu--horizontal .el-menu-item {
  height: 40px;
  line-height: 40px;
}
.el-menu--horizontal .el-submenu > .el-menu {
  position: fixed;
  left: 180px;
  width: 180px;
  min-width: 0;
  overflow-x: hidden;
  border-radius: 0;
  max-height: 500px;
  top: inherit;
  margin-top: -40px;
  background: #fff;
  box-shadow: none;
  border: 1px solid #ccc;
  padding: 0;
  z-index: 9999;
}
.el-menu--horizontal .el-submenu .el-submenu__title,
.el-menu--horizontal .el-menu-item {
  height: 40px;
  line-height: 40px;
  border-bottom: none;
  width: 180px;
}
.secondMenu .el-menu::-webkit-scrollbar-track-piece {
  background: #e8e8e8;
}
.secondMenu .el-menu::-webkit-scrollbar {
  width: 6px;
}
.secondMenu .el-menu::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #afafaf;
}
.el-menu-item-group__title {
  padding: 10px 0 0 10px;
  box-sizing: border-box;
  color: #a9c4bc;
  margin: 3px 0;
}
.el-menu-item.is-active {
  color: #fff;
  background-color: #00C26F;
}



