
.page-login {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-image: url("../../../static/login.jpg");
  background-color: #8ff1ff;
  background-position: center;
  background-size: cover;
}
.page-login .el-button.btn-login:active,
.page-login .el-button.btn-login:hover,
.page-login .el-button.btn-login:focus,
.page-login .el-button.btn-login:visited {
  color: #fff;
  outline: 0;
}
.page-login .el-button.btn-login:hover {
  background-color: #00d67b;
  border-color: #00d67b;
}
.page-login .el-button.btn-login:active {
  background-color: #00ae63;
  border-color: #00ae63;
}
.login-titlePannel {
  position: absolute;
  left: 100px;
  top: 50%;
  margin-top: -280px;
  transform: translateY(-50%);
  text-align: left;
}
.login-inputPannel {
  padding-top: 3%;
  /*.right{*/
  /*}*/
}
.login-inputPannel .left {
  width: 50%;
  float: left;
  margin-top: 130px;
  text-align: center;
}
.login-inputPannel .left img {
  width: 450px;
}
.login-inputPannel .wrapper {
  width: 50%;
  max-width: 350px;
  margin-left: 41%;
  text-align: center;
}
.login-inputPannel .form-content {
  background: #ffffff;
  padding: 0 30px 30px 30px;
  border: 1px solid transparent;
  border-radius: 10px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.2);
}
.login-inputPannel .form-content .font-labelI {
  font-size: 18px;
  color: #9e9e9e;
  line-height: 60px;
  font-weight: 600;
}
.login-inputPannel .icon_user label {
  width: 30px;
  height: 30px;
  float: left;
  padding: 8px 0;
  background-position: right;
  background-repeat: no-repeat;
  background-image: url("../../assets/ico/icon_user.png");
  background-size: auto 18px;
  font-size: 0;
}
.login-inputPannel .icon_psd label {
  width: 30px;
  height: 30px;
  float: left;
  padding: 8px 0;
  background-position: right;
  background-repeat: no-repeat;
  background-image: url("../../assets/ico/icon_password.png");
  background-size: auto 18px;
  font-size: 0;
}
.login-inputPannel .el-form-item {
  border-radius: 5px;
  background-color: #fff;
  border: 1px solid #9e9e9e;
}
.login-inputPannel .el-form-item .el-input__inner {
  border-width: 0;
}
.login-inputPannel .el-form-item.is-error {
  border-color: #ff4949;
}
.login-inputPannel .formItem__ifRemember {
  float: left;
  color: #9e9e9e;
}
.login-inputPannel .formItem__ifRemember .el-checkbox__label {
  font-size: 12px;
}
.login-inputPannel .formItem__psdForgetUrl {
  float: right;
  font-size: 12px;
  color: #9e9e9e;
}
.login-inputPannel .formItem__iconUser .el-form-item__content {
  width: auto;
  margin-left: 44px;
}
.login-inputPannel .formItem {
  display: block;
  margin-bottom: 15px;
  line-height: 30px;
  text-align: center;
}
.login-inputPannel .formItem .font-label {
  font-size: 24px;
  font-weight: bold;
}
@media screen and (max-width: 1500px) {
.login-inputPannel .formItem .font-label {
    font-size: 20px!important;
}
}
.login-inputPannel .formItem__logo {
  line-height: 40px;
}
.login-inputPannel .formItem__logo img {
  height: 80px;
  vertical-align: middle;
}
.login-inputPannel .formItem__input .el-input__inner {
  border-radius: 0;
}
.login-inputPannel .formItem__btn .el-button--primary {
  width: 100%;
  padding: 13px 15px;
  font-size: 14px;
  border-radius: 6px;
  background-color: #00C26F;
  border-color: #00C26F;
}
.login-footPannel {
  width: 100%;
  height: 35px;
  line-height: 35px;
  position: absolute;
  left: 0;
  bottom: 20px;
  text-align: center;
  color: #fff;
}
.login-footPannel .footer-icon {
  width: 25px;
  margin: 0 5px;
  vertical-align: middle;
}
.login-btn-loading {
  background-color: #eef1f6;
  border-color: #eef1f6;
}
.loading-img {
  width: 10px;
  margin-left: 5px;
}
.connect-div {
  margin-top: 10px;
}
.connect-div a {
  font-weight: bold;
  font-size: 14px;
  color: white;
}



